html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-padding-top: 0rem;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-optical-sizing: auto;
  font-style: normal;
  color: #000000;
  font-size: 2rem;
  background-color: #0F1624;
  overflow-x: hidden;
}

.contenedor {
  width: 90%;
  max-width: 120rem;
  margin: 0 auto;
}

.m-0 {
  margin: 0;
}

a {
  font-size: 2rem;
  text-decoration: none;
}

h1, h2, h3 {
  margin: 0 0 5rem 0;
  font-weight: 900;
}

h1 {
  font-size: 5rem;
}

h2 {
  font-size: 4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2rem;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

svg {
  width: 100%;
}

#typed::after {
  content: "|";
  animation: blink 0.7s infinite;
}

@keyframes blink {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
}
.typed-cursor {
  display: none;
}

#scrollTopBtn {
  position: fixed;
  bottom: 40px;
  right: 40px;
  width: 60px;
  height: 60px;
  border: none;
  border-radius: 50%;
  background: #fff;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
  cursor: pointer;
  z-index: 1000;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: opacity 0.3s;
}

#scrollTopBtn .scroll {
  position: absolute;
  width: 24px;
  height: 24px;
  fill: #14ACC7;
  z-index: 2;
}

/* Progress Circle */
#scrollTopBtn svg.progress-circle {
  position: absolute;
  top: 0;
  left: 0;
  width: 60px;
  height: 60px;
  transform: rotate(-90deg);
  z-index: 1;
}

.track {
  fill: none;
  stroke: #ddd;
  stroke-width: 4;
}

.progress {
  fill: none;
  stroke: #3498db;
  stroke-width: 4;
  stroke-dasharray: 100;
  stroke-dashoffset: 100;
  transition: stroke-dashoffset 0.25s linear;
}

#scrollTopBtn.hide {
  opacity: 0;
  pointer-events: none;
}

.header {
  padding: 5rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 1200px) {
  .header {
    flex-direction: row;
    justify-content: space-between;
  }
}
.header h1 {
  color: #FFFFFF;
  font-weight: 900;
  font-size: 3rem;
}
@media (min-width: 768px) {
  .header h1 {
    margin-bottom: 2rem;
  }
}
@media (min-width: 1200px) {
  .header h1 {
    margin-bottom: 0;
  }
}
.header h1 span {
  color: #14ACC7;
  text-shadow: 0 0 20px rgba(20, 172, 199, 0.6);
}

.navegacion {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .navegacion {
    flex-direction: row;
    margin-top: 0;
  }
}
.navegacion__enlace {
  font-size: 1.5rem;
  color: #FFFFFF;
  text-align: center;
}
.navegacion__enlace:hover {
  color: #14ACC7;
  text-shadow: 0 0 20px rgba(20, 172, 199, 0.6);
}

.presentacion {
  padding: 5rem 0;
  display: flex;
  flex-direction: column-reverse;
}
@media (min-width: 1200px) {
  .presentacion {
    display: grid;
    grid-template-columns: 2fr 1fr;
    align-items: center;
    column-gap: 8rem;
  }
}
.presentacion h2 {
  font-size: 5rem;
  max-width: 50rem;
  color: #FFFFFF;
  text-align: center;
}
@media (min-width: 768px) {
  .presentacion h2 {
    max-width: 100%;
    text-align: center;
  }
}
@media (min-width: 1200px) {
  .presentacion h2 {
    text-align: left;
  }
}
.presentacion__posicion {
  background: linear-gradient(135deg, #935ED6, #14ACC7);
  background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 3rem;
  font-weight: 700;
  text-align: center;
}
@media (min-width: 1200px) {
  .presentacion__posicion {
    text-align: left;
  }
}
.presentacion__texto {
  color: #929EB0;
  text-align: center;
  line-height: 2;
}
@media (min-width: 768px) {
  .presentacion__texto {
    text-align: justify;
  }
}
.presentacion__redes {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 3rem;
}
@media (min-width: 1200px) {
  .presentacion__redes {
    justify-content: start;
  }
}

.linkedin,
.github,
.gmail {
  width: 4rem;
  height: 4rem;
  transition: transform 0.3s ease-in-out;
}
.linkedin:hover,
.github:hover,
.gmail:hover {
  transform: scale(1.5);
}

.circular {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  border: 1px solid #14ACC7;
  padding: 0.8rem;
  box-shadow: -1px 2px 43px -11px rgb(20, 172, 199);
  transition: box-shadow 0.3s ease-in-out;
}
.circular:hover {
  box-shadow: -1px 2px 43px 5px rgb(20, 172, 199);
}

.perfil {
  position: relative;
  width: 280px;
  height: 280px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: -6rem auto 4rem auto;
}
@media (min-width: 768px) {
  .perfil {
    width: 380px;
    height: 380px;
  }
}
@media (min-width: 768px) {
  .perfil {
    margin: -4rem auto 10rem auto;
  }
}
@media (min-width: 1200px) {
  .perfil {
    margin: 0;
  }
}
.perfil__circular {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background: linear-gradient(135deg, #935ED6, #14ACC7); /* Fondo degradado */
  padding: 8px; /* Espacio para el borde */
  box-shadow: -1px 2px 43px -11px rgb(20, 172, 199);
  position: relative;
  transition: box-shadow 0.3s ease-in-out;
}
.perfil__circular:hover {
  box-shadow: -1px 2px 43px 5px rgb(20, 172, 199);
}
.perfil__imagen {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  object-fit: cover;
  border: 2px solid #0F1624;
  margin: 0 auto;
}

.habilidades {
  padding: 5rem 0;
}
.habilidades h2 {
  color: #FFFFFF;
}
.habilidades__contenedor {
  position: relative;
  border-radius: 1rem;
  padding: 3rem;
  overflow: hidden;
  z-index: 1;
}
.habilidades__contenedor::before {
  content: "";
  position: absolute;
  inset: 0;
  padding: 2px;
  border-radius: 1rem;
  background: linear-gradient(270deg, #a855f7, #3b82f6, #06b6d4, #3b82f6, #a855f7);
  background-size: 400% 400%;
  animation: runBorder 8s linear infinite;
  mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
  -webkit-mask-composite: xor;
  mask-composite: exclude;
  z-index: -1;
}
@media (min-width: 768px) {
  .habilidades__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
@media (min-width: 1200px) {
  .habilidades__grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 5rem;
  }
}
.habilidades__img {
  width: 5rem;
  height: 5rem;
}
.habilidades__nombre {
  margin: 1.5rem 0 0 0;
  color: #FFFFFF;
  font-weight: 700;
}

/* Animación que hace correr el degradado */
@keyframes runBorder {
  0% {
    background-position: 0% 50%;
  }
  100% {
    background-position: 400% 50%;
  }
}
.html,
.css,
.js,
.sass,
.gulp,
.node,
.vscode,
.figma,
.html,
.boostrap,
.tailwind {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  border-radius: 1rem;
  border: 1px solid #14ACC7;
  padding: 4rem;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .html,
  .css,
  .js,
  .sass,
  .gulp,
  .node,
  .vscode,
  .figma,
  .html,
  .boostrap,
  .tailwind {
    margin-bottom: 0;
  }
}

.servicios {
  padding: 5rem 0;
}
.servicios h2 {
  color: #FFFFFF;
}
@media (min-width: 768px) {
  .servicios__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 4rem;
  }
}
@media (min-width: 1200px) {
  .servicios__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 4rem;
  }
}

.servicio {
  background-color: rgb(10.5, 15.4, 25.2);
  box-shadow: 0px 0px 27px 0px rgba(20, 172, 199, 0.75);
  padding: 4rem;
  border-radius: 1rem;
  color: #FFFFFF;
  position: relative;
  z-index: 1;
  overflow: hidden;
  margin-bottom: 4rem;
  transition: transform 0.3s ease-in-out;
}
@media (min-width: 768px) {
  .servicio {
    margin-bottom: 0;
  }
}
.servicio::before {
  content: "";
  position: absolute;
  inset: 0;
  padding: 2px;
  border-radius: 1rem;
  max-height: 50rem;
  background: linear-gradient(270deg, #a855f7, #3b82f6, #06b6d4, #3b82f6, #a855f7);
  background-size: 400% 400%;
  animation: runBorder 8s linear infinite;
  mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
  -webkit-mask-composite: xor;
  mask-composite: exclude;
  z-index: -1;
}
.servicio__imagen {
  max-width: 5rem;
  width: 100%;
  margin-bottom: 2rem;
}
.servicio__titulo {
  margin: 0;
  font-weight: 600;
  font-size: 2.2rem;
}
.servicio__texto {
  font-weight: 200;
  line-height: 2;
  max-height: 30rem;
  font-size: 1.6rem;
}
.servicio:hover {
  transform: scale(1.1);
  cursor: pointer;
}

.proyectos {
  padding: 5rem 0;
}
.proyectos__titulo {
  color: #FFFFFF;
}
@media (min-width: 768px) {
  .proyectos__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 4rem;
  }
}
@media (min-width: 1200px) {
  .proyectos__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 4rem;
  }
}

.proyecto {
  background-color: rgb(10.5, 15.4, 25.2);
  border: 1px solid #14ACC7;
  border-radius: 1rem;
  overflow: hidden;
  transition: transform 0.3s ease-in-out;
  margin-bottom: 2rem;
  max-height: 60rem;
}
@media (min-width: 768px) {
  .proyecto {
    margin-bottom: 0;
  }
}
.proyecto:hover {
  transform: scale(1.1);
}
.proyecto__imagen {
  margin-bottom: 2rem;
}
.proyecto__imagen img {
  height: 18rem;
}
.proyecto__informacion {
  padding: 2rem;
  min-height: 10rem;
}
.proyecto__titulo {
  margin: 0;
  color: #FFFFFF;
  font-weight: 600;
  font-size: 2.2rem;
}
.proyecto__descripcion {
  color: #FFFFFF;
  line-height: 1.5;
  font-weight: 200;
  font-size: 1.7rem;
}
.proyecto__botones {
  display: flex;
  align-items: center;
  gap: 2rem;
}
.proyecto__boton {
  background: #14ACC7;
  background: linear-gradient(90deg, rgb(20, 172, 199) 0%, rgb(147, 94, 214) 100%);
  font-size: 1.5rem;
  color: #FFFFFF;
  padding: 1rem 3rem;
  border-radius: 1rem;
}

.contacto {
  padding: 5rem 0;
}
.contacto__titulo {
  text-align: center;
  color: #FFFFFF;
  margin: 0;
}
.contacto__centrar {
  display: flex;
  justify-content: center;
  margin-top: 3rem;
}

.footer {
  padding: 3rem 0;
}
.footer__texto {
  color: #FFFFFF;
  text-align: center;
  font-size: 1.5rem;
  margin-top: 4rem;
}
.footer__centrar {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: -6rem;
}

.panel {
  padding: 4rem 2rem;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.panel__contenedor {
  background-color: #121f38;
  padding: 3rem;
  border-radius: 12px;
  max-width: 600px;
  width: 100%;
  text-align: center;
  box-shadow: 0 0 20px rgba(20, 172, 199, 0.3);
}
.panel__titulo {
  font-size: 2rem;
  margin-bottom: 1rem;
  color: #ffffff;
}
.panel__descripcion {
  font-size: 1rem;
  color: #d1d5db;
  margin-bottom: 2rem;
}
.panel__boton {
  display: inline-block;
  padding: 0.75rem 1.5rem;
  font-size: 1rem;
  border: none;
  border-radius: 8px;
  text-decoration: none;
  background-color: #14ACC7;
  color: #fff;
  transition: background-color 0.3s ease;
}
.panel__boton:hover {
  background-color: #1097b0;
}

.crear-proyecto {
  background-color: #0F1624;
  padding: 4rem 2rem;
  border-radius: 1rem;
  max-width: 800px;
  margin: 0 auto;
}
.crear-proyecto__titulo {
  color: #FFFFFF;
  text-align: center;
  font-size: 2.5rem;
  margin-bottom: 3rem;
}
.crear-proyecto__formulario {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
.crear-proyecto__campo {
  display: flex;
  flex-direction: column;
}
.crear-proyecto__etiqueta {
  color: #FFFFFF;
  margin-bottom: 0.8rem;
  font-weight: bold;
}
.crear-proyecto__input, .crear-proyecto__textarea {
  padding: 1rem;
  border: none;
  border-radius: 0.5rem;
  font-size: 1.6rem;
  background-color: #1E2A3A;
  color: #FFFFFF;
}
.crear-proyecto__input::placeholder {
  color: #ccc;
}
.crear-proyecto__textarea {
  resize: vertical;
}
.crear-proyecto__centrar {
  display: flex;
  justify-content: center;
}
.crear-proyecto__boton {
  background-color: #14ACC7;
  color: #FFFFFF;
  border: none;
  padding: 1.2rem 3rem;
  font-size: 1.6rem;
  border-radius: 0.5rem;
  cursor: pointer;
  transition: background-color 0.3s ease;
}
.crear-proyecto__boton:hover {
  background-color: rgb(15.3424657534, 131.9452054795, 152.6575342466);
}

.alerta {
  padding: 1rem;
  color: #FFFFFF;
  text-align: center;
  font-weight: 700;
  text-transform: uppercase;
  margin: 1rem 0;
}
.alerta.error {
  background-color: #14ACC7;
}